@import url(https://fonts.googleapis.com/css?family=Open+Sans|Oswald|Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  /* background-color: rgb(231, 242, 250); */
  cursor: default;
}
.root {
  width: 100vw;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Welcome {
  margin-top: 15vh;
  align-items: center;
  justify-content: center;
  display: flex;
}
.WelcomeBox {
  width: 80vw;
  height: 40vh;
  color: rgb(95, 122, 241);
  text-align: center;
  align-items: center;
}
.StartButton {
  font-size: calc(2.2vmin);
  background: rgb(250, 198, 56);
  color: white;
  text-align: center;
  max-height: 4vh;
  max-width: 25vw;
  min-width: 10vw;
  font-weight: 600;
  padding: 2%;
  margin: auto;
  border-radius: 25px;
}

.HomeImg {
  width: 30%;
  margin-bottom: 1%;
}

.WelcomeTitle {
  font-family: "Marck Script";
  font-weight: 500;
  font-size: 2em;
}
input[type="radio"] {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  width: 0;
}

.ChoiceItems:hover {
  background-color: rgb(255, 216, 109);
  color: cornflowerblue;
  cursor: pointer;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex: 25% 1;
  max-width: 60%;
}
.ChoiceAreaBox {
  margin-top: 1rem;
}
.ChoiceArea {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: auto;
}
.ChoiceAreaSubmit {
  display: block;
  margin: 1% auto;
}
.ChoiceItems {
  text-align: center;
  color: white;
  display: flex;
  font-size: 2.5vh;
  font-weight: 600;
  background-color: rgba(111, 139, 233, 0.863);
  min-width: 7vw;
  align-items: center;
  width: 27vw;
  vertical-align: middle;
  height: 10vh;
  margin: 0.5vw;
  justify-content: center;
}

.ChoiceText {
  padding: 5%;
}
.SubmitButton {
  text-align: center;
  margin: 1% auto;
  display: block;
  padding: 5px;
  color: white;
  background-color: cornflowerblue;
  width: 20vw;
  height: 7vh;
  font-size: 2.5vmin;
  letter-spacing: 1px;
  font-family: Spartan;
  font-weight: 600;
  border-radius: 25px;
  border: none;
}
.SubmitButton:focus {
  outline: 0;
}
.SubmitButton:hover {
  cursor: pointer;
}
.QuestionNumber {
  margin: auto;
  margin-bottom: 2%;
  font-weight: 500;
  color: white;
  font-size: 3rem;
  text-align: center;
  font-family: "Voltaire";
}

.QuestionText {
  margin: 2% auto;
  align-items: center;
  font-weight: 600;
  color: rgb(255, 216, 109);
  font-size: 4vw;
  text-align: center;
  font-family: "Voltaire";
}

.QuestionTextBox {
  margin: auto;
  align-items: center;
  width: 75vw;
  min-height: 25vh;
}

.QuestionContainer {
  width: "100%";
  background-color: "#6b94e6";
  margin-bottom: 5%;
}
@media only screen and (max-width: 800px) {
  .StartButton {
    min-width: 55vw;
    min-height: 6vh;
    font-size: 4vmin;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .HomeImg {
    width: 70%;
  }
  .ChoiceArea {
    min-width: 90vw;
  }
  .ChoiceItems {
    min-width: 40vw;
    margin: 1vw;
    min-height: 7vh;
  }
}
@media only screen and (max-width: 550px) {
  .ChoiceItems {
    min-width: 40vw;
    margin: 1vw;
    min-height: 7vh;
    display: flex;
    justify-content: center;
  }
  .ChoiceText {
    font-size: 70%;
  }
  .ChoiceAreaSubmit {
    width: 100vw;
  }
  .SubmitButton {
    margin-top: 5%;
    width: 50vw;
    height: 7vh;
    font-size: 2vh;
  }
  .QuestionTextBox {
    width: 100vw;
  }
  .QuestionText {
    font-size: 6vh;
  }
  @media only screen and (max-width: 400px) {
    .ChoiceArea {
      width: 100vw;
    }
    .ChoiceItems {
      min-width: 70vw;
      min-height: 10vh;
    }
  }
}

.results {
  width: 100%;
}
.congratsBanner {
  text-align: center;
  color: rgb(61, 123, 238);
  font-size: 3em;
  font-weight: 600;
}
.singleResultBox {
  width: 50vw;
  max-height: 70vh;
  margin: auto;
  background-color: white;
  color: rgb(61, 123, 238);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
}
.restaurantTitle {
  font-size: 1vw;
  margin-bottom: 10%;
  font-weight: 600;
  text-align: center;
}
.restaurantImage {
  width: 100%;
  min-height: 20vh;
  text-align: -webkit-center;
  font-weight: 700;
  color: white;
  font-size: 1.2rem;
  letter-spacing: 0.7rem;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
h1.restaurantSuper {
  padding-top: 6%;
  padding-bottom: 6%;
  text-align: center;
  margin: auto;
}
.restaurantDescription {
  margin-top: 4%;
  margin-bottom: 4%;
  font-size: 1.5vw;
  font-weight: 600;
  padding: 1%;
  text-align: center;
}
.restaurantInfoBox {
  padding-top: 2%;
  padding-bottom: 2%;
  width: 50vw;
  text-align: center;
  display: block;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.restaurantMoney {
  width: 12%;
  margin: 5px;
}
@media screen and (max-width: 800px) {
  .singleResultBox {
    width: 90vw;
    max-height: 80vh;
    border-radius: 5px;
  }
  .restaurantInfoBox {
    padding-top: 2%;
    padding-bottom: 2%;
    width: 90vw;
    text-align: center;
    display: block;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .restaurantImage {
    width: 100%;
    min-height: 20vh;
    text-align: -webkit-center;
    font-weight: 700;
    color: white;
    font-size: 0.8rem;
    letter-spacing: 0.7rem;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  .restaurantDescription {
    font-size: 3vw;
  }
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
}
.AppHome {
  font-family: "Spartan";
  font-size: 80%;
  width: 100vw;
  height: 100vh;
  background-image: url(/beourguest/static/media/background.b2db7d39.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.AppQuiz {
  font-family: "Spartan";
  font-size: 80%;
  width: 100vw;
  height: 100vh;
  background-image: url(/beourguest/static/media/background3.b7e8d5a9.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.AppLogo {
  max-height: 3rem;
  display: inline;
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.AppHeader {
  background-color: #6b94e6;
  max-height: 7vh;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: center;
  justify-content: flex-start;
}

.AppTitle {
  display: inline;
  font-family: "Marck Script";
  font-size: 1.4em;
  font-weight: 500;
  word-spacing: -0.3em;
}
.AppLogo:hover {
  cursor: pointer;
}
.AppTitle:hover {
  cursor: pointer;
  color: rgb(255, 216, 109);
}

